import React, {Component} from 'react';
import {Container,Header,Segment} from 'semantic-ui-react';
import '.././styles.css';

class News extends Component{
    render(){
        return (
            <Segment className='ui padbelow' inverted basic>
                <Container className="whoop">
                    <Story title='First Update' date='4/6/2020'>
                        No news yet, but as they say, no news is good news.
                    </Story>
                </Container>
            </Segment>
        );
    }
}

class Story extends Component{
    render(){

        return(
            <Container>
                <Header
                    className='ui color1'
                    as='h2'>
                    {this.props.title}
                    <Header
                        className='ui color1'
                        as ='h3' content={this.props.date}/>
                </Header>
                <p className='newssize'>
                    {this.props.children}
                </p>
            </Container>
        );
    }
}

export default News;