import React, {Component} from 'react';
import {Container,Segment} from 'semantic-ui-react';
import '.././styles.css';

class Resume extends Component{
    render(){
        return (
            <Segment className='ui padbelow' inverted basic>
                <Container className="whoop">
                    <h1>Resume</h1>
                    <p className='newssize'>
                        Coming soon.
                    </p>
                </Container>
            </Segment>


        );
    }
}

export default Resume;